import React from 'react'
import styled from 'styled-components'
import Announcements from '../components/Announcements';
import Footer from '../components/Footer'; 
import Navbar from '../components/Navbar';
import Newsletter from '../components/Newsletter';
import {Add,Remove} from '@material-ui/icons'
import image_021 from '../images/image_021.webp'


const Container = styled.div``;

const Wrapper= styled.div`
    padding:50px;
    display:flex;
     
`;


const ImgContainer = styled.div`
    flex:1;

`;
const Image = styled.img`
    width:100%;
    height:90vh;
    object-fit:cover;
    @media (max-width: 425px) {
        height:40vh;
    };
 
    
`;
const InfoContainer = styled.div`
    flex:1;
    padding:0px 50px;
    @media (max-width: 425px) {
        padding:10px;
    };
    
`;
const Title = styled.h1`
    font-weight:200;
`;
const Desc = styled.p`
    margin:20px 0px;
`;
const Price = styled.span`
    font-weight:100;
    font-size:40px;
`;

const FilterContainer = styled.div`
    display:flex;
    width:50%;
    margin:30px 0px;
    justify-content:space-between;
    @media (max-width: 425px) {
        width:100%;
    }
 
`;
const Filter = styled.div`
    display:flex;
    align-items:center;
`;
const FilterTitle = styled.span`
    font-size:20px;
    font-weight:200;
`;
const FilterColor = styled.div`
    width:20px;
    height:20px;
    border-radius:50%;
    background-color: ${props=> props.color}; 
    margin:0px 5px;
    cursor:pointer;
`;
const FilterSize = styled.select`
    margin-left:10px;
    padding:5px;
`;
const FilterSizeOption = styled.option``;
const AddContainer= styled.div`
    width:50%;
    display:flex;
    align-items:center;
    justify-content:space-between;
    @media (max-width: 425px) {
        width:100%;
    }
    
`;
const AmountContainer = styled.div`
    display:flex;
    align-items:center;
    font-weight:700;
`; 
const Amount = styled.span`
    width:30px;
    height:30px;
    border-radius:10px;
    border:1px solid teal;
    display:flex;
    align-items:center;
    justify-content:center;
    margin:0px 5px;
`;
// const Button= styled.button`
//     padding:15px;
//     border:2px solid teal;
//     background-color:white;
//     cursor:pointer;
//     font-weight:500;

//     &:hover{
//         background-color:#f8f4f4; 
//     }
// `
const Product = () => {
  return (
    <Container>
        <Announcements/>
        <Navbar/>
        

        <Wrapper>
            <ImgContainer>
                <Image src={image_021}/>
            </ImgContainer>
            <InfoContainer>
                <Title>Gilbeys</Title>
                <Desc>Lorem ipsum dolor sit amet consectetur adipisicing elit. Consequuntur maxime perferendis id iste, eveniet quae nemo minima, deserunt a illo vel voluptatum nisi omnis? Esse officiis et praesentium illum veniam.</Desc>
                <Price>$ 20</Price>
            <FilterContainer>
                <Filter>
                    <FilterTitle>Flavour</FilterTitle>
                    <FilterColor color="black"/>
                    <FilterColor color="darkblue"/>
                    <FilterColor color="gray"/>
                </Filter>
                <Filter>
                    <FilterTitle>Quantity</FilterTitle>
                    <FilterSize>
                        <FilterSizeOption>5 Litles</FilterSizeOption>
                        <FilterSizeOption>3 Litles</FilterSizeOption>
                        <FilterSizeOption>1 Litles</FilterSizeOption>
                        <FilterSizeOption>500 ml</FilterSizeOption>
                    </FilterSize>
                </Filter>
            </FilterContainer>
            <AddContainer>
                <AmountContainer>
                    <Remove/>
                    <Amount>1</Amount>
                    <Add/>
               </AmountContainer>
            </AddContainer>
            </InfoContainer>

        </Wrapper>

 


        <Newsletter/>
        <Footer/>
    </Container>
  )
}

export default Product